import styled from "styled-components";
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";
import MapTools from "./MapTools";
import MapMarker from "./MapMarker";

const Container = styled.img``;

const MapView = ({
	mapZoom,
	mapItem,
	mapPosition,
	imgW,
	imgH,
	infoPanel,
	setInfoPanel,
}) => {
	return (
		<TransformWrapper
			defaultScale={1}
			scale={mapZoom}
			options={{
				limitToBounds: true,
				limitToWrapper: false,
			}}
			positionX={-mapPosition.x}
			positionY={-mapPosition.y}
			onPanning={() => {
				// onPanningStart, remove the onclick-transition class (0.5s animation)
				//  so panning doesn't feel clunky
				const el = document.getElementsByClassName(
					"react-transform-element"
				)[0];
				el.classList.remove("onclick-transition");

				// add dragging class, so we can determine if the destination was clicked on for real,
				//  of if the destination was clicked while panning
				el.classList.add("dragging");
			}}
			onPanningStop={() => {
				// remove dragging class
				const el = document.getElementsByClassName(
					"react-transform-element"
				)[0];
				el.classList.remove("dragging");
			}}
		>
			{({ zoomIn, zoomOut, resetTransform, ...rest }) => (
				<>
					<MapTools
						zoomIn={zoomIn}
						zoomOut={zoomOut}
						resetTransform={resetTransform}
					/>

					<TransformComponent>
						<MapMarker
							x={mapPosition.x}
							y={mapPosition.y}
							mapItem={mapItem}
							setInfoPanel={() => {
								setInfoPanel();
							}}
							infoPanel={infoPanel}
						/>
						<Container
							src="./images/map.jpg"
							width={imgW}
							height={imgH}
						></Container>
					</TransformComponent>
				</>
			)}
		</TransformWrapper>
	);
};

export default MapView;
