import styled from "styled-components";
import { MdKeyboardReturn } from "react-icons/md";
import { IoIosKeypad } from "react-icons/io";
import { useState } from "react";
import LegendData from "./LegendData";
import SideMenuItem from "./SideMenuItem";

const colours = {
	menuBorder: "#7e7e7e",
	menuBg: "#c0b8b0",
	menuItem: "#b3ada2",
	menuItemHover: "#ebc277",
	menuTab: "#fdfdfd",
	menuTabBg: "#a0998c",
	menuTabBgHover: "#ada89f",
};

const Container = styled.div`
	position: absolute;
	width: 300px;
	height: 100vh;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	background: ${colours.menuBg};
	z-index: 20;
	transition: all 0.3s ease;
	/* opacity: 0; */
	left: ${(props) => (props.open ? "-300px" : "0px")};
	scrollbar-width: 0;

	&::-webkit-scrollbar {
		display: none;
	}

	@media only screen and (max-device-width: 768px) {
		width: ${(props) => (props.open ? "0vw" : "100vw")};
		opacity: ${(props) => (props.open ? "0" : "1")};
	}
`;

const Menu = styled.div`
	position: relative;
	width: 100%;
	height: calc(100% - 70px);
	display: flex;
	flex-direction: column;
	justify-content: start;
	align-items: flex-end;
`;

const MenuTab = styled.div`
	position: absolute;
	display: flex;
	cursor: pointer;
	align-items: center;
	justify-content: center;
	border-radius: ${(props) => (props.open ? "0 0 20px 0" : "0 0 0 0")};
	font-size: 2.4em;
	width: 70px;
	height: 70px;
	position: absolute;
	color: ${(props) => (props.open ? colours.menuTab : "black")};
	background: ${(props) => (props.open ? "black" : colours.menuTabBg)};
	top: 0;
	left: ${(props) => (props.open ? "0px" : "230px")};
	transition: all 0.2s ease;
	scale: 1;
	z-index: 21;

	&:hover {
		background: ${(props) => (props.open ? "#2f2f2f" : colours.menuTabBgHover)};
		scale: ${(props) => (props.open ? "1.05" : "1.0")};
	}
`;

const Banner = styled.div`
	background: ${colours.menuTabBg};
	width: 300px;
	height: 70px;
	display: flex;
	text-indent: 20px;
	align-items: center;
	font-size: 1.7em;
	position: absolute;
	top: 0;
	left: 0;
	font-family: "Charm", cursive;
`;

const SideMenu = ({ itemClick, currentItem }) => {
	const [open, setOpen] = useState(false);

	const data = LegendData;

	return (
		<>
			<MenuTab
				open={open}
				onClick={() => {
					setOpen(!open);
				}}
			>
				{open ? <IoIosKeypad /> : <MdKeyboardReturn />}
			</MenuTab>
			<Container open={open}>
				<Banner>Legend</Banner>
				<Menu>
					{data.map((item, index) => {
						return (
							<SideMenuItem
								currentItem={currentItem}
								item={item}
								key={index}
								clicked={itemClick}
							/>
						);
					})}
				</Menu>
			</Container>
		</>
	);
};

export default SideMenu;
