import styled from "styled-components";
import { GiWhiteTower } from "react-icons/gi";
import { FaCity, FaMountain } from "react-icons/fa";
import { BiDownArrow, BiUpArrow } from "react-icons/bi";
import { useState } from "react";

const colours = {
	menuBorder: "#7e7e7e",
	menuBg: "#c0b8b0",
	menuItem: "#b3ada2",
	menuItemHover: "#ebc277",
	menuTab: "#fdfdfd",
	menuTabBg: "#a0998c",
	menuTabBgHover: "#ada89f",
};

const StyledP = styled.p`
	padding-left: 10px;
	transition: all 0.3s ease;
`;

const MenuItem = styled.div`
	position: relative;
	display: flex;
	justify-content: left;
	align-items: center;
	padding-left: 20px;
	width: calc(100% - 40px);
	font-size: 1.4em;
	height: 50px;
	color: black;
	cursor: pointer;
	transition: all 0.3s ease;
	opacity: 0.7;
`;
const ItemWrapper = styled.div`
	position: relative;
	height: 50px;
	display: flex;
	align-items: center;
	width: calc(100%);
	transition: all 0.3s ease;
	background: ${colours.menuItem};

	&:hover {
		background: ${colours.menuItemHover};
	}

	&:hover ${MenuItem} {
		opacity: 1;
		margin-left: 4px;
	}
	&:hover ${StyledP} {
		padding-left: 14px;
	}
`;

const DropDown = styled.div`
	position: relative;
	height: 50px;
	display: flex;
	align-items: center;
	transition: all 0.3s ease;
	background: ${colours.menuItem};
	padding-left: 20px;
	width: calc(100% - 20px);
	font-size: 1.4em;
	height: 50px;
	color: black;
	cursor: pointer;
	transition: all 0.3s ease;
	opacity: 0.7;
`;

const arrowStyle = {
	fontSize: "1em",
	paddingLeft: "10px",
};

const activeStyle = {
	background: "#e0a639",
};

const SideMenuItem = ({ item, clicked, currentItem }) => {
	const [subMenu, setSubMenu] = useState(false);

	const showSubMenu = () => setSubMenu(!subMenu);

	return (
		<>
			<ItemWrapper>
				<DropDown onClick={item.menu && showSubMenu}>
					{item.title}
					{!subMenu ? (
						<BiDownArrow style={arrowStyle} />
					) : (
						<BiUpArrow style={arrowStyle} />
					)}
				</DropDown>
			</ItemWrapper>
			{item.menu &&
				subMenu &&
				item.menu.map((i, index) => {
					return (
						<ItemWrapper style={i === currentItem ? activeStyle : null}>
							<MenuItem
								onClick={() => {
									if (i !== currentItem) {
										clicked(i);
									}
								}}
							>
								{i.type === "Ashmount" ? (
									<FaMountain />
								) : i.type === "Major City" ? (
									<GiWhiteTower />
								) : i.type === "Minor City" ? (
									<FaCity />
								) : null}
								<StyledP>{i.title}</StyledP>
							</MenuItem>
						</ItemWrapper>
					);
				})}
		</>
	);
};

export default SideMenuItem;
