import styled from "styled-components";
import { FaMapMarkerAlt, FaScroll } from "react-icons/fa";

const Marker = styled.div`
	position: absolute;
	width: auto;
	height: auto;
	color: #ff6161;
	font-size: 3em;
	border-radius: 20px;
	left: ${(props) => props.x - 1 + "px"};
	top: ${(props) => props.y + "px"};
	opacity: ${(props) => (props.x == 0 ? "0" : "1")};
	z-index: 10;
	animation: pulsate 4s cubic-bezier(0.19, 1, 0.22, 1) infinite;
	transition: all 0.3s;

	@keyframes pulsate {
		0% {
			transform: translate(-50%, -50%);
			color: rgba(59, 32, 32, 0.438);
		}
		50% {
			transform: translate(-50%, -60%);
			color: rgba(187, 41, 41, 0.856);
		}
		100% {
			transform: translate(-50%, -50%);
			color: rgba(59, 32, 32, 0.472);
		}
	}
`;

const Note = styled.div`
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	width: auto;
	height: auto;
	padding: 10px 20px 10px 20px;
	color: #272727;
	background: #af2626;
	font-size: 1.8em;
	border-radius: 10px;
	left: ${(props) => props.x + "px"};
	top: ${(props) => props.y + "px"};
	z-index: 10;
	transform: translate(20%, -60%);
	font-family: "Charm", cursive;
	cursor: pointer;
	transition: all 0.8s cubic-bezier(0.19, 1, 0.22, 1);
	text-indent: 10px;
	opacity: ${(props) => (props.x == 0 ? "0" : "1")};
	border: 2px solid transparent;

	:hover {
		background: rgba(202, 24, 24, 0.788);
		border: 2px solid #000000;
	}
`;

const MapMarker = ({ x, y, mapItem, setInfoPanel, infoPanel }) => {
	let title;

	if (mapItem === null) {
		title = "";
	} else {
		title = mapItem.title;
	}

	return (
		<>
			<Note
				x={x}
				y={y}
				onClick={() => {
					setInfoPanel();
				}}
				showing={infoPanel}
			>
				<FaScroll />
				{title}
			</Note>
			<Marker x={x} y={y}>
				<FaMapMarkerAlt />
			</Marker>
		</>
	);
};

export default MapMarker;
