import "./App.css";
import SideMenu from "./components/SideMenu";
import MapView from "./components/MapView";
import styled from "styled-components";
import { useEffect, useState } from "react";
import LegendData from "./components/LegendData";

const quotes = [
	"The best liars are those who tell the truth most of the time.",
	"Don’t worry that you aren’t giving people what they want. Give them who you are, and let that be enough.",
	"Let them see my weakness, and let them see me overcome it.",
	"A man can only stumble for so long before he either falls or stands up straight.",
	"Nothing is truly complete until the day it is finally destroyed",
	"A man was defined not by his flaws, but by how he overcame them.",
	"Men rarely see their own actions as unjustified.",
	"Those who take lightly promises they make to those they love are people who find little lasting satisfaction in life.",
	"A good king is one who is trusted by his people- and one who deserves that trust",
	"The trick is to never stop looking. There's always another secret.",
	"There's always another secret...",
	"Don't forget the ash because you see a little silk.",
	"Belief is often strongest when it should be weakest. That is the nature of hope.",
];

const NoteDescription = styled.div`
	position: absolute;
	font-size: 1em;
	top: ${(props) => (props.show ? "calc(100% - 300px)" : "100%")};
	left: 0;
	height: 300px;
	width: 100%;
	background: white;
	z-index: 10;
	cursor: default;
	transition: all 0.5s ease;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	display: flex;
	justify-content: end;
`;

const MistOverlay = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	display: ${(props) => (props.doAnimate ? "block" : "none")};
	width: 100%;
	height: 100%;
	z-index: 30;
	background: rgb(207, 207, 207);
	opacity: 1;
`;

const Mist = styled.div`
	position: absolute;
	background: ${(props) => 'url("./images/' + props.img + '")'};
	background-size: cover;
	animation: ${(props) =>
		props.img === "fog-high.png"
			? "animateUp 8s infinite ease"
			: "animateDown 8s infinite ease"};
	width: 100%;
	height: 100%;

	@keyframes animateUp {
		0% {
			transform: translate(0%, 0%) scale(1.25);
			opacity: 1;
		}
		100% {
			transform: translate(0%, -100%);
			opacity: 0;
		}
	}
	@keyframes animateDown {
		0% {
			transform: translate(0%, 0%) scale(1.25);
			opacity: 1;
		}
		100% {
			transform: translate(0%, 100%);
			opacity: 0;
		}
	}
`;

const Graphic = styled.div`
	position: relative;
	width: 100vw;
	height: 100vh;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: "Mirza", cursive;
	animation: ${(props) => "intro " + props.duration + "s ease"};
	@keyframes intro {
		0% {
			opacity: 1;
			font-size: 2em;
			letter-spacing: 2px;
		}
		50% {
			opacity: 0.97;
		}
		100% {
			opacity: 0;
			font-size: 2.05em;
			letter-spacing: 3px;
		}
	}
`;

let realMapPosition = { x: 0, y: 0 };
let realMapScale = 1;

function App() {
	const defaultItem = {
		title: "Empty",
		description: "",
		type: "",
		position: {
			x: 0,
			y: 0,
		},
	};

	// original dimensions of the Map image
	const originalW = 4048;
	const originalH = 3036;

	const imgW = window.innerWidth;
	const imgH = window.innerWidth * (9 / 16);

	const [mapZoom, setMapZoom] = useState(1);
	const [mapPosition, setMapPosition] = useState({ x: 0, y: 0 });
	// const [realMapPosition, setRealMapPosition] = useState({ x: 0, y: 0 });
	const [mapItem, setMapItem] = useState(defaultItem);
	const [infoPanel, setInfoPanel] = useState(false);

	const [doAnimate, setDoAnimate] = useState(false);
	const [introAnimation, setIntroAnimation] = useState(0);

	// allow mouse is set as false when the map is
	//  undergoing a transition (after clicking a map item)
	const [allowMouse, setAllowMouse] = useState(true);

	// set the focused map item from the sideMenu
	const setFocusedItem = (item) => {
		if (item === null) {
			return;
		}

		// if the item position is within the viewport - 200px, don't change map position

		// real coordinates of the map destination
		const x = parseFloat(item.position.x * parseFloat(imgW / originalW)),
			y = parseFloat(item.position.y * parseFloat(imgH / originalH));

		setMapPosition({ x: x, y: y });
		setMapItem(item);

		let el = document.getElementsByClassName("react-transform-element")[0];
		el.classList.remove("hover-cursor");
	};

	// when clicking a menu item, set Map zoom and selected item
	const handleMenuClick = (item) => {
		setIntroAnimation(introAnimation + 1);

		setDoAnimate(true);

		// set the focused item, which also sets the mapPosition X,Y
		setFocusedItem(item);

		item.position.x !== 0 && item.position.y !== 0
			? setMapZoom(2)
			: setMapZoom(1);
	};

	// function used with handleMapClick to determine
	//  realMapPosition and realMapScale,
	//		via the react-transform-element styles,
	//		which includes the translate and scale options for transform
	const getMapOffsetScale = (e) => {
		// extract the react-transform-element style: translate(x, y) to get the PositionX,Y
		let transform = e.target.style.transform;
		// Get translate values
		let translate = transform
			.slice(transform.indexOf("(") + 1, transform.indexOf(")"))
			.replaceAll("px", "")
			.trim()
			.replaceAll(" ", "");

		// Get/set the scale
		let scale = transform
			.slice(transform.indexOf("scale(") + 6, transform.length)
			.replace(")", "")
			.trim();
		realMapScale = parseFloat(scale);

		let arr = translate.split(",");

		// Set the realMapPosition
		realMapPosition = {
			x: parseFloat(arr[0]) * -1,
			y: parseFloat(arr[1]) * -1,
		};
		if (isNaN(realMapPosition.x)) {
			realMapPosition.x = 0;
		}
		if (isNaN(realMapPosition.y)) {
			realMapPosition.y = 0;
		}
	};

	// function for clicking over map, check coordinates to see if a destination has been clicked
	const handleMapClick = (e) => {
		// first calculate the overflow offset of the Map PositionX,Y
		getMapOffsetScale(e);

		// get mouse coordinates within the map image
		const mouseX =
				parseFloat((e.clientX + realMapPosition.x) / realMapScale) *
				parseFloat(originalW / imgW),
			mouseY =
				parseFloat((e.clientY + realMapPosition.y) / realMapScale) *
				parseFloat(originalH / imgH);

		LegendData.forEach((item) => {
			item.menu &&
				item.menu.forEach((map_item) => {
					// DEBUGGING PURPOSES as Ashmounts don't have bounds {x,y,x2,y2}
					if (map_item.type === "Ashmount") {
						return;
					}

					if (
						mouseX >= map_item.bounds.x &&
						mouseY >= map_item.bounds.y &&
						mouseX <= map_item.bounds.x2 &&
						mouseY <= map_item.bounds.y2
					) {
						// prevent user from selected the same item again
						if (map_item === mapItem) {
							return;
						}
						// add the transition to the map classlist,
						//  it is removed when panning in MapView
						e.target.classList.add("onclick-transition");

						// set the focus of the map to new item
						setFocusedItem(map_item);
						// set the map Zoom
						setMapZoom(2);
						// disallow the mouse from being used until the onclick-transition has ended
						//  this is called in onTransitionEnded() in div.container
						setAllowMouse(false);
					}
				});
		});
	};

	const handleMapItemHover = (e) => {
		getMapOffsetScale(e);

		// convert mouse coordinates to full size map image
		const mouseX =
				parseFloat((e.clientX + realMapPosition.x) / realMapScale) *
				parseFloat(originalW / imgW),
			mouseY =
				parseFloat((e.clientY + realMapPosition.y) / realMapScale) *
				parseFloat(originalH / imgH);

		// hoveringAnItem is incremented if the user is hovering over a map item,
		//  and decremented otherwise
		let hoveringAnItem = 0;
		// activeItemCount is the count of all items which have a 'bounds' property
		let activeItemCount = 0;

		// therefore, we can check the difference between hoveringAnItem and activeItemCount
		//  to see if the user is currently hovering an item.
		//  this will let us add the '.hover-cursor' class afterwards.

		LegendData.forEach((item) => {
			item.menu &&
				item.menu.forEach((map_item) => {
					// DEBUGGING PURPOSES as Ashmounts don't have 'bounds' yet {x,y,x2,y2}
					if (!map_item.bounds) {
						return;
					} else {
						activeItemCount++;
					}

					// increment hoveringAnItem if the cursor is over a mapItem
					if (
						mouseX >= map_item.bounds.x &&
						mouseY >= map_item.bounds.y &&
						mouseX <= map_item.bounds.x2 &&
						mouseY <= map_item.bounds.y2
					) {
						hoveringAnItem++;
					} else {
						hoveringAnItem--;
					}
				});
		});

		let cursorIsOverMapItem = -hoveringAnItem !== activeItemCount;
		// add or remove the hover-cursor class if the cursor is over the map item
		if (cursorIsOverMapItem) {
			e.target.classList.add("hover-cursor");
		} else {
			e.target.classList.remove("hover-cursor");
		}
	};

	// mist animation/quote on app load
	useEffect(() => {
		if (introAnimation === 0) {
			setDoAnimate(true);
			setIntroAnimation(introAnimation + 1);
		}
	});

	return (
		<div
			id="container"
			className="container"
			onMouseUp={(e) => {
				if (!allowMouse) {
					e.preventDefault();
					e.stopPropagation();
					return;
				}

				// only perform handleMapClick if the map wasn't being panned
				if (!e.target.classList.contains("dragging")) {
					if (e.target.classList[0] === "react-transform-element") {
						handleMapClick(e);
					}
				}
			}}
			onMouseDown={(e) => {
				if (!allowMouse) {
					e.preventDefault();
					e.stopPropagation();
					return;
				}
			}}
			onMouseMove={(e) => {
				if (!allowMouse) {
					return;
				}
				if (e.target.classList[0] === "react-transform-element") {
					handleMapItemHover(e);
				}
			}}
			onTransitionEnd={(e) => {
				if (e.elapsedTime === 1) {
					setAllowMouse(true);
				}
			}}
		>
			{/* On menuItemClick, change mapItem. Keep track of mapItem to set the active item */}
			<SideMenu itemClick={handleMenuClick} currentItem={mapItem} />

			{/* The map which uses TransformWrapper/TransformComponent */}
			<MapView
				mapPosition={mapPosition}
				mapZoom={mapZoom}
				mapItem={mapItem}
				imgW={imgW}
				imgH={imgH}
				setInfoPanel={() => {
					setInfoPanel(!infoPanel);
				}}
				infoPanel={infoPanel}
			/>

			{/* Mist overlay for changing map destinations/page load effect */}
			<MistOverlay
				doAnimate={doAnimate}
				onAnimationEnd={() => {
					setDoAnimate(false);
					console.log();
				}}
				className={
					doAnimate ? (introAnimation === 1 ? "fade-intro" : "fade") : ""
				}
			>
				<Mist img="fog-high.png" />
				<Mist img="fog-low.png" />
				{introAnimation === 1 && (
					<Graphic duration={8}>
						{"“" + quotes[Math.floor(Math.random() * quotes.length)] + "”"}
						<br />
						<br />
						{"-Brandon Sanderson"}
					</Graphic>
				)}
				{introAnimation > 1 && <Graphic duration={2}>{mapItem.title}</Graphic>}
			</MistOverlay>

			{
				<NoteDescription show={infoPanel}>
					{mapItem && mapItem.description}
				</NoteDescription>
			}
		</div>
	);
}

export default App;
