import styled from "styled-components";
import { MdZoomOutMap } from "react-icons/md";
import { AiOutlineZoomIn, AiOutlineZoomOut } from "react-icons/ai";

const ToolBar = styled.div`
	position: absolute;
	display: flex;
	left: 50%;
	transform: translateX(-50%);
	height: 70px;
	width: auto;
	padding-top: 10px;
	z-index: 19;
	justify-content: center;
`;

const ToolItem = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	width: 70px;
	height: 70px;
	border-radius: 5px;
	background: #4d4d4d;
	box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
	-o-transition: all 0.4s ease-in-out;
	-webkit-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
	color: #eee;
	cursor: pointer;
	margin: 0 2px;
	font-size: 3em;

	:hover {
		background: #3b3b3b;
		color: #d46e6e;
	}
`;

const MapTools = ({ zoomIn, zoomOut, resetTransform }) => {
	return (
		<ToolBar>
			<ToolItem onClick={resetTransform}>
				<MdZoomOutMap />
			</ToolItem>

			<ToolItem onClick={zoomIn}>
				<AiOutlineZoomIn />
			</ToolItem>

			<ToolItem onClick={zoomOut}>
				<AiOutlineZoomOut />
			</ToolItem>
		</ToolBar>
	);
};

export default MapTools;
